<template>
  <div>
    <mt-header title="选择预约时间" color="rgb(0,204,153)">
    	<router-link to="/home" slot="left">
    		<mt-button icon="back">返回</mt-button>
    	</router-link>
    </mt-header>
    <ele-calendar @pick="pick" :render-content="renderContent" :data="datedef" :prop="prop"></ele-calendar>
    <div class="appoint" @click="appoint">马上预约</div>
  </div>
</template>

<script>
  import eleCalendar from 'ele-calendar'
  import 'ele-calendar/dist/vue-calendar.css'
  import { Indicator,Toast } from 'mint-ui'
  const axios = require('axios')
  export default {
    data() {
      return {
        datedef: [], //选中样式数组 只要按他的格式放入数组中 就会被渲染
        value: new Date(),
        prop: 'Date', //对应日期字段名
        num: '',
        selectDate:'',
        endData:'',
        listIndex:'',
        selectValue:''
      }
    },
    components: {
      eleCalendar
    },
    methods:{
      GetHospitalDate() {
        axios({
          method: 'post',
          url: 'https://www.c65z45.cn/api/Reserve/GetHospitalDate',
          data: {
            HospitalID: this.listIndex,
            Token: 'B68DE9D1F8584D7099BCE47144D0351C'
          }
        }).then(res => {
          Indicator.close()
          if(res.data.IsSuccess){
            this.datedef = res.data.Data
          }else{
            Toast({
              message: '加载失败',
              position: 'middle',
              duration: 3000
            })
          }
        }).catch(error => {
          Indicator.close()
          Toast({
              message: '服务器错误',
              position: 'middle',
              duration: 3000
            })
          })
      },
      pick(data,event,row,dom){
        // console.log(data,event,row,dom)
         this.selectRow = row
         this.selectData = data
         this.selectValue = row.value
      },
      appoint () {
        if(!this.selectRow){
          Toast({
            message: '请选择日期',
            duration: 3000
          });
        }else if(!this.selectRow.value){
          Toast({
            message: '当前日期不可选',
            duration: 3000
          });
        }else if(this.selectRow.value.SelectNumber >= this.selectRow.value.Number){
          Toast({
            message: '当前日期预约人数已满',
            duration: 3000
          });
        }else{
          let d = new Date(this.selectData)
          this.endData = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate();
          this.$router.push({
						path: '/appoint',
						query: {selectValue:this.selectValue}
					})
        }
      },
      renderContent(h, parmas) {
          const loop = data => {
            return data.defvalue.value ? ( <
              div class = "flex2 selected" > {
                data.defvalue.text
              } <
              div style = "font-size:12px;"> {
                data.defvalue.value.SelectNumber
              }
              / {data.defvalue.value.Number}< /div > <
              /div>
            ) : ( <
              div class = "flex2" style = "font-size:20px;line-height:60px;color:#c0c4cc;"> {
                data.defvalue.text
              }  < /
              div >
            )
          }
          return parmas.defvalue.value.SelectNumber && parmas.defvalue.value.SelectNumber == parmas.defvalue.value.Number?(<div style = "min-height:60px;background:red;color:#fff;" > {
            loop(parmas)
          } < /div>):(<div style = "min-height:60px;font-size:16px;" > {
            loop(parmas)
          } < /div>)
        }
      },
      created(){
        this.listIndex = this.$route.query.listIndex
        this.GetHospitalDate()
      },
      mounted(){
        Indicator.open('加载中...')
      }
    }
</script>

<style scoped>
  .appoint{
  	margin: 0.3rem auto 0 auto;
  	width: 95%;
  	background: rgb(0,204,153);
  	color: #fff;
  	text-align: center;
  	line-height: 1rem;
  	border-radius: 0.1rem;
  	font-size: 0.35rem;
  	font-weight: 600;
  }
  .mint-header{
  	background: rgb(0,204,153);
  }
</style>
